import { Button, message, Popconfirm, Tag } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { Fn } from "../../../helper/function";
import { PaginationRequest, TableColumn } from "../../../models/baseModel";
import { StoredManageService } from "../../../services/adminService";
import { MyBox } from "../../core/MyBox";
import { MyButton } from "../../core/MyButton";
import { OrderByModel, PageInfoModel } from "../../core/TableModel";
import TableComponent from "../../core/TableNewComponent";
import { UIFormatDateTimeVNComponent } from "../../core/UI_FormatComponent";

export default function ManageStoredManageIndexComponent() {
    const column = [
        new TableColumn({ title: "STT", dataIndex: "stt", key: "stt", disabled: true }),
        new TableColumn({ title: "Tên", dataIndex: "name", key: "name" }),
        new TableColumn({
            title: "Trạng thái", dataIndex: "status", key: "status", render: (el: number) => {
                let value = "";
                let color = "blue";
                if (el === 0) {
                    value = "Developing";
                    color = "green";
                }

                if (el === 1) {
                    value = "Đã deploy";
                    color = "blue";
                }

                if (el === 2) {
                    value = "Đang fix";
                    color = "yellow";
                }

                if (el === -1) {
                    value = "Đã xóa";
                    color = "red";
                }
                return (
                    <Tag color={color}>{value}</Tag>
                )
            }
        }),
        new TableColumn({
            title: "Ngày deploy Dev", key: "dateDeployDev", dataIndex: "dateDeployDev", render: (row: string) => {
                return (<UIFormatDateTimeVNComponent value={row} />);
            }
        }),
        new TableColumn({
            title: "Ngày deploy Product", key: "dateDeployProduct", dataIndex: "dateDeployProduct", render: (row: string) => {
                return (<UIFormatDateTimeVNComponent value={row} />);
            }
        }),
        new TableColumn({
            title: "Hành động", key: "action", dataIndex: "action", render: (row: string, data: any) => {
                return (
                    <div>
                        <Button type="link">
                            <Link to={`/admin/storedManage/edit/${data.key}`}>Chỉnh sửa</Link>
                        </Button>

                        <Popconfirm
                            title="Are you sure to delete this stored?"
                            onConfirm={() => {
                                handleClickDeleteStored(data.key);
                            }}>
                            <Button type="link">
                                Xóa
                            </Button>
                        </Popconfirm>
                    </div>
                );
            }
        }),
    ];

    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());
    const [isLoading, setIsLoading] = useState<boolean>(false);
    const [data, setData] = useState([]);

    const loadData = useCallback(() => {
        setIsLoading(true);
        var paginationRequest = new PaginationRequest(pageInfo.pageNumber, pageInfo.pageSize, orderBy);
        StoredManageService.GetList(paginationRequest, (data: any) => {
            setPageInfo(new PageInfoModel(data));
            let row = data.data.data.pagedData.map((data: any, index: number) => {
                return {
                    key: data.key,
                    stt: index + 1,
                    name: data.name,
                    status: data.status,
                    dateDeployDev: data.dateDeployDev,
                    dateDeployProduct: data.dateDeployProduct
                };
            })
            setData(row);
        }, undefined, () => {
            setIsLoading(false);
        })
    }, [pageInfo.pageNumber, pageInfo.pageSize, orderBy]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function handleClickDeleteStored(key: string) {
        setIsLoading(true);
        StoredManageService.GetDetail(key, (data: any) => {
            StoredManageService.Delete(key, Fn.GetTimestamp(data), (data: any) => {
                message.success(data.data.message);
                loadData();
            }, () => {
                setIsLoading(false);
            })
        }, () => {
            setIsLoading(false);
        })
    }

    return (
        <MyBox
            title="Quản lý stored procedure"
            isLoading={isLoading}
            buttonList={() => (
                <div>
                    <Link to="/admin/storedManage/add"><MyButton type="primary" value="Thêm mới" /></Link>
                    <Link to="/admin/storedManage/linkStored"><MyButton type="secondary" value="Liên kết stored" /></Link>
                </div>
            )}>
            <TableComponent
                orderBy={orderBy}
                setOrderBy={setOrderBy}
                pageInfo={pageInfo}
                setPageInfo={setPageInfo}
                column={column}
                dataSource={data}
                loading={isLoading}
                loadData={loadData} />
        </MyBox>
    );
}